<template>
<!-- 待办 -- 审批、提交送审 -->
<!-- bug：审批人下拉老滑动不了 -->
<!-- ok: 方案管理、非检修项目计划、大检修管理手册（审批人没测、审批提交没测）、委托设计（重审 多人的时候数据可能不对） -->
  <div class="examine">
    <navBar :svgIcon='svgIcon' :titleText="titleText" @changeClose='changeClose'></navBar>
    <div class="examine-body" >
      <div v-if="titleText == '审核'">
        <p>审批流程</p>
        <van-dropdown-menu get-container="body" >
          <van-dropdown-item v-model="processValue" :title='processTitle' :options="processOption" disabled @change='handelChange'/>
        </van-dropdown-menu>
        <p>审批意见</p>
        <textarea v-model="description" cols="30" rows="5" placeholder="请输入"></textarea>
        <p>审批结果 <span style="color: #f20909">*</span></p>
        <van-dropdown-menu get-container="body" :overlay='false'>
          <van-dropdown-item :title='resultTitle' @open='handelOpen'/>
        </van-dropdown-menu>
        <div v-show="resultTitle == '同意' && isShowNextPrincipals">
          <div v-for="(item, index) in principalss" :key="index">
            <!-- bug -->
            <p>{{item.custom}}审批人 <span style="color: #f20909">*</span></p>
            <van-dropdown-menu get-container="body" :overlay='true' :direction="index >= 2 ? 'up' : 'down'">
              <!-- <van-dropdown-item v-model="value" :title='peopleTitle' :options="peopleOption" @change="selectPeople"/> -->
              <van-dropdown-item v-model="subForm.principals[index]" :title='subForm.principalsOptionTitle' :options="principalsOption[index]" @change="selectPeople"/>
            </van-dropdown-menu>
          </div>
        </div>

        <!-- 审批结果弹出按钮 -->
        <van-popup v-model="show" position="bottom" :style="{ height: '25.5%' }">
          <van-cell is-link center :title="item" v-for="(item, i) in btnList" :key="i" label="" @click="handelBtn(item)"/>
        </van-popup>
      </div>
      <div v-else>
        <p>工作流模板 <span style="color: #f20909">*</span></p>
        <van-dropdown-menu get-container="body" :overlay='false'>
          <van-dropdown-item v-model="workflowTemplatesValue" :title='workflowTemplatesTitle' :options="workflowTemplates" @change='selectWorkflow'/>
        </van-dropdown-menu>
        <div v-for="(item, index) in principalss" :key="index">
          <p>{{item.custom}}审批人 <span style="color: #f20909">*</span></p>
          <van-dropdown-menu get-container="body" :overlay='true' :direction="index >= 2 ? 'up' : 'down'">
            <!-- {{subForm.principals}} -->
            <van-dropdown-item v-model="subForm.principals[index]" :title='subForm.principalsOptionTitle' :options="principalsOption[index]" @change='selectSubPeople'/>
          </van-dropdown-menu>
        </div>
        <!-- <p>部门审批人</p>
        <van-dropdown-menu get-container="body" :overlay='false'>
          <van-dropdown-item v-model="processValue" :title='processTitle' :options="processOption" @change='handelChange'/>
        </van-dropdown-menu>
        <p>部门审批人</p>
        <van-dropdown-menu get-container="body" :overlay='false'>
          <van-dropdown-item v-model="processValue" :title='processTitle' :options="processOption" @change='handelChange'/>
        </van-dropdown-menu>
        <p>部门审批人</p>
        <van-dropdown-menu get-container="body" :overlay='false'>
          <van-dropdown-item v-model="processValue" :title='processTitle' :options="processOption" @change='handelChange'/>
        </van-dropdown-menu> -->
        <p>描述</p>
        <textarea v-model="description" cols="30" rows="5" placeholder="请输入"></textarea>
      </div>

      <van-button v-show="btnShow" round type="info" block @click="submit">提交</van-button>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import navBar from '@/components/navBar.vue';
import { Notify } from 'vant'
export default {
  name: 'Examine',
  components: {
    navBar,
  },
  watch: {
    '$store.state.btnShow'(newVal) {
      console.log('watch', newVal)
      this.btnShow = newVal;
    }
  },
  data() {
    return {
      titleText: '审核',
      value: '',
      title: '请选择',
      resultTitle: '请选择',
      processValue: '',
      processTitle: '请选择', //  流程标题
      processOption: [],  //  流程数据
      whichUnit: '',  //  无用 删

      labels: {}, //  无用 闲的时候删掉
      peopleTitle: '',  //  
      peopleOption: [], //  施工单位检查人
      svgIcon: require('@/assets/close.svg'),
      show: false,
      btnList: ['同意', '不同意', '回退至上一步'],
      description: '', //  描述
      approvalId: null,
      businessId: null,
      isShowNextPrincipals: false,

      flows: [],
      nodesData: [],
      orgValue: '',
      currentNode: '',
      isOnLastPoint: false,
      principalss: [],
      

      // 提交 同意 跟 回退至上一步参数字段
      nextPrincipals: [{userId: ""}],
      flowAction: '',
      annotation: '',
      isNumberOne: false,

      //  提交送审数据
      workflowTemplatesValue: '',
      workflowTemplatesTitle: '',
      workflowTemplates: [],

      subForm: {
        principals: [],
        principalsOptionTitle: ''
      },  //  提交送审的表单
      principalsOption: [],
      principal: {},
      principals: [],

      btnShow: true
    }
  },
  methods: {
    changeClose() {
      this.$emit('changeClose')
    },
    handelChange(val) {
      debugger
      this.processOption.forEach(obj => {
        if (obj.value === val) {
          this.processTitle = obj.text
          this.processValue = val
          this.labels = obj.labels
        }
      })
      this.getApprovalFlowTemplates(val)
    },
    handelOpen() {
      this.show = true;
    },
    handelBtn(item) {
      // debugger
      if (this.isNumberOne && item == '回退至上一步') {
        alert('当前节点不可用');
        this.show = false;
        return;
      }
      this.resultTitle = item;
      this.show = false;
      this.flowAction = item == '同意' ? 'AGREE' : item == '不同意' ? 'DISAGREE' : item == '回退至上一步' ? 'ROLLBACK' : '';
    },
    selectPeople(val) {
      return;
      this.peopleOption.forEach(obj => {
        if (obj.value === val) {
          this.peopleTitle = obj.text
          this.value = val
        }
      })
      // this.principals[0].userId = val;
      this.nextPrincipals[0].userId = val;
      
    },


    selectWorkflow(val) { //  选择工作流模板
      this.subForm.principals = [];
      this.workflowTemplates.forEach(obj => {
        if (obj.value === val) {
          this.workflowTemplatesTitle = obj.text
          this.workflowTemplatesValue = val
        }
      })
      this.getApprovalFlowTemplates(val)
    },
    selectSubPeople(val) { //  选择提交送审的人员

    },


    submit() {
      // 有问题 ，pc端提交也报错（非检修项目计划）
      console.log('isShowNextPrincipals', this.isShowNextPrincipals)
      if (this.titleText == '审核') {
        if (this.resultTitle == '请选择') {
          alert('请选择审批结果');
          return;
        }
        if (this.resultTitle == '同意' && this.isShowNextPrincipals) {
          // if (!this.nextPrincipals[0].userId && this.principalss.length != 0) {  
        if (this.subForm.principals && this.subForm.principals.length != this.principalss.length) {
            alert('请选择审批人');
            return;
          }
        }
        if (this.resultTitle == '不同意' || this.resultTitle == '回退至上一步') {
          this.nextPrincipals = [];
        }
      } else {
        if (this.subForm.principals.length == 0) {
          alert('请选择审批人');
          return;
        }
      }

      if (this.titleText == '审核') {
        let arr = [];
        this.subForm.principals.forEach(item=> {
          arr.push({userId: item})
        })

        const params = new FormData();
        params.append('nextPrincipals', JSON.stringify(arr))
        params.append('flowAction', this.flowAction)
        params.append('annotation', this.resultTitle + '。' + this.description)
        this.$postHttp(`${this.$baseUrl}/OS-PM/approvalFlow/approvals/${this.approvalId}`,
        params
        ).then(res=> {
          console.log('提交成功，同意、回退至上一步', res)
          if (res.status == 200) {
  
          }
        }).catch(err=> {
          Notify(({ type: 'danger', message: err.response.data.message}))
        })
      } else {
        // 传参时为空..
        // const params = new FormData();
        // params.append('templateId', this.workflowTemplatesValue)
        // let arr = [];
        // this.subForm.principals.forEach(item=> {
        //   arr.push({userId: item})
        // })
        // params.append('principals', JSON.stringify(arr))
        // params.append('description', '回退重审。' + this.description)
        // params.append('createdStateName', '提交送审')

        let arr1 = [];
        this.subForm.principals.forEach(item=> {
          arr1.push({userId: item})
        })
        let obj = {
          templateId:this.workflowTemplatesValue,
          principals: JSON.stringify(arr1),
          description: '回退重审。' + this.description,
          createdStateName: '提交送审'
        }
        // console.log('new FormData()', params)
        this.$putHttp(`${this.$baseUrl}/OS-PM/versions/${window.localStorage.getItem('versionId')}/approvalFlow/approvals/${this.approvalId}/updated`, obj, {isFormData: true}).then(res=> {
          console.log('updated', res);
          if (res.status == 200) {
            
          }
        }).catch(err=> {

        })
      }


      // const params = new FormData();
      // params.append('templateId', this.processValue)
      // params.append('businessId', this.businessId)
      // params.append('principals', JSON.stringify(this.principals))
      // params.append('description', this.description)
      // params.append('createdStateName', '提交送审')
      // this.$postHttp(`${this.$baseUrl}/OS-PM/versions/${this.$store.getters.getVersionId}/approvalFlow/approvals`, 
      // params
      // ).then(res=> {
      //   console.log('提交', res)
      // }).catch(err=> {
      //   console.log('提交失败', err)
      //   Notify(({ type: 'danger', message: err.response.data.message}))
      // })
      this.$router.push({name: 'WaitDealt'});
    },

    async examineInit (module, approvalId, detailsInfo, principal) { //  获取所有审批流程
      this.reset();
      this.titleText = '审核';
      this.principal = principal; //  当前是为了方案管理模块加的，后续不知道有没有问题暂不确定
      if (module == '方案管理') {
        this.orgValue = detailsInfo.auditOrganization //  貌似是无用的
      }
      this.approvalId = approvalId;
      this.getApprovalFlowExample(approvalId);
      let retrunData = await this.$getHttp(`${this.$baseUrl}/OS-PM/approvalFlow/approvals/${approvalId}/isOnLastPoint`);
      console.log('retrunData', retrunData)
      this.isOnLastPoint = retrunData.data;
      console.log('this.isOnLastPoint', this.isOnLastPoint)
      
    },

    getApprovalFlowExample(approvalId) { //  获取指定审批流实例
      this.$getHttp(`${this.$baseUrl}/OS-PM/approvalFlow/approvals/${approvalId}`, 
      ).then(res=> {
        console.log('获取指定审批流实例', res)
        if (res.status == 200) {
          this.businessId = res.data.businessId;
          // res.data.currentApprovalPointId; 用于判断 人员的表单名字拼什么
          // res.data.templateId
          this.getApprovalFlowTemplates(res.data.templateId, res.data.currentApprovalPointId);

  				let len = 0
          res.data.principals.forEach(ele => {
            if(ele.usedFlowAction && ele.usedFlowAction === 'AGREE') {
              len++
            }
          })
          this.isShowNextPrincipals = len === res.data.principals.length - 1 ? true : false

        }
      }).catch(err=> {
      })
    },

    // organization/findOrgName/{orgName}     直接传orgValue；
    // organization/getUsers
    getApprovalFlowTemplates(templateId, currentApprovalPointId) { // 获取-指定审批流模板 *审批流程*
      this.$getHttp(`${this.$baseUrl}/OS-PM/approvalFlow/templates/${templateId}`).then(res=> {
        if (res.status == 200) {
          this.processTitle = res.data.name;
          res.data.approvalPoints.forEach(item=> {
            if (item.id == currentApprovalPointId) {
              this.whichUnit = item.principals[0].custom
            }
          })

          this.flows = res.data.flows
          this.nodesData = res.data.approvalPoints
          this.nodesData.forEach(element => {
            element.numberBefore = ''
          })
          res.data.flows.forEach(element => {
            const index = this.nodesData.findIndex(item => item.id === element.srcApprovalPointId) + 1
            const indexBefore = this.nodesData.findIndex(item => item.id === element.destApprovalPointId)
            this.nodesData[indexBefore].numberBefore = index === 0 ? '' : index
          })
          this.isNumberOne = this.isNumberOnePoint(currentApprovalPointId);
          console.log('this.isNumberOne', this.isNumberOne)
          if(this.titleText == '重审') {
            let numberOne = -1
            this.nodesData.forEach((element, index) => {
              if (element.numberBefore === '') {
              numberOne = index
              return
              }
            })
            this.principalss = this.nodesData[numberOne].principals

          } else {
            console.log('到这了吗')
            if (!this.isOnLastPoint) {
              let numberOne = -1
							let nextId = -1
							this.flows.forEach((element, index) => {
								if (element.srcApprovalPointId === currentApprovalPointId) {
									nextId = element.destApprovalPointId
									return
								}
							})
            console.log('真呢')

							this.nodesData.forEach((element, index) => {
								if (element.id === nextId) {
									numberOne = index
									return
								}
							})
							if(numberOne !== -1) {
								this.principalss = this.nodesData[numberOne].principals
							}
            }
          }
            console.log('this.principalss', this.principalss, 'this.principal', this.principal);

          for(let index = 0; index < this.principalss.length; index++) {
              const element = this.principalss[index]

              this.principalsOption[index] = []

              if (element.orgType === 'STATIC') {
                console.log('element.orgType', element.orgType)
                this.getFindOrgName(index, element.orgValue)
              } else {
                let isHave = false
                for (const key in this.principal) {
                  if (key === element.orgValue) {
                    isHave = true
                    let arr = this.principal[key].split(',')
                    for(let i = 0; i < arr.length; i++) {
                      this.getFindOrgName(index, arr[i])
                    }
                    let map = new Map()
                    for (let item of this.principalsOption[index]) {
                        map.set(item.id, item)
                    }
                    this.$set(this.principalsOption, index, [...map.values()])
                  }
                }
                if (!isHave) {
                  // this.$message({
                  // 	showClose: true,
                  // 	duration: 1000,
                  // 	message: `${element.orgValue}匹配不到负责人`,
                  // 	type: 'warning'
                  // })
                }
                // if (this.titleText != '提交送审') {
                //   console.log('this.orgValue', this.orgValue)
                //   this.getFindOrgName(index, this.orgValue)
                //   return;
                // }
              }
            }
        }
        console.log('指定审批流模板', res)
        // STATIC： STATIC 直接拿orgValue； F 
      }).catch(err=> {

      })
    },
	  isNumberOnePoint(currentApprovalPointId) { // 判断是不是第一个节点
      let isNumberOne = false
      const index = this.nodesData.findIndex(item => item.id === currentApprovalPointId)
      if(index !== -1) {
        isNumberOne = this.nodesData[index].numberBefore === '' ? true : false
      }
      return isNumberOne
		},
    getFindOrgName(index, orgName) {  //  根据名称查id
      this.$postHttp(`${this.$baseUrl}/OS-PM/organization/findOrgNameByOrg/${orgName}`).then(res=> {
        console.log('findOrgNameByOrg', res)
        if (res.status == 200) {
          this.getUsers(index, res.data);
        }
      })
    },
    getUsers(index, orgId) { //  查看组织机构下人员
      this.peopleOption = [];
      this.principalsOption[index] = [];
      let mediumArr = [];
      this.$postHttp(`${this.$baseUrl}/OS-PM/organization/getUsers`, {orgId}).then(res=> {
        console.log('getUsers', res)
        if (res.status == 200) {
          res.data.results.forEach(e => {
            const obj = {
              text: e.userName,
              value: e.id,
            }
            this.peopleOption.push(obj);
            // this.principalsOption[index].push(obj);
            mediumArr.push(obj)
          })
          this.$set(this.principalsOption, index, [...this.principalsOption[index], ...mediumArr]);
          console.log('this.principalsOption[index].push(obj);', this.principalsOption);
        }
      })
    },
    async submitInit(modulE, approvalId, principal) {
      this.reset();
      this.titleText = '重审';
      this.principal = principal;
      this.approvalId = approvalId;
      let res = await this.$getHttp(`${this.$baseUrl}/OS-PM/approvalFlow/templates`, {
        params: {
          labels: {流程所属模块: modulE}
        }
      })
      res.data.results.forEach(e => {
        const obj = {
          text: e.name,
          value: e.id,
          labels: e.labels
        }
        this.workflowTemplates.push(obj);
      })
      if (this.workflowTemplates) {
        this.workflowTemplatesTitle = this.workflowTemplates[0].text;
        this.workflowTemplatesValue = this.workflowTemplates[0].value;
        this.getApprovalFlowTemplates(this.workflowTemplates[0].value)
      }
      console.log('this.workflowTemplates', this.workflowTemplates);
    },
    reset() {  //  初始化操作 
      this.processValue = ''
      this.processTitle = '请选择'
      this.processOption = []
      this.description = ''
      this.resultTitle = '请选择'

      this.workflowTemplates = [];
      this.workflowTemplatesTitle = '';
      this.subForm.principals = [];
    },
  },
}
</script>

<style lang='less' scoped>
  * {
    padding: 0;
    margin: 0;
  }
  .examine {
    height: 100%;
    overflow: hidden;
    .examine-body {
      box-sizing: border-box;
      // background: aqua;
      height: calc(100% - 24rem);
      padding: 1rem;
      padding-top: 0rem;
      p {
        margin-top: 1rem;
        margin-bottom: 0.25rem;
      }
      textarea {
        width: 100%; 
        resize:none;
        margin-top: -0.1rem;  
        border-radius: 0.5rem;
        border-color: #B4B4B4;
        box-sizing: border-box;
        padding: 1rem;
      }
      /deep/ .van-dropdown-menu__bar {
        border: 1px solid #808187;
        border-radius: 0.5rem;
      }
      .van-button {
        width: 90%;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        bottom: 2rem;
      }
      .van-cell {
        // padding: 0rem 1rem 0rem 1rem;
        padding: 1rem;
        .van-icon-arrow:before {
          content: ''
        }
      }
    }
  }
</style>