<template>
<!-- 公共头部导航栏 -->
  <div class="nav-bar">
    <van-nav-bar @click-left="clickLeft" @click-right="clickRight">
      <template #left>
        <img :src="svgIcon">
      </template>
      <template #title>
        {{titleText}}
        <slot></slot>
      </template>
      <template #right>
        <img :src="svgIconRight">
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  name: 'NavBar',
  props: {
    svgIcon: {
      type: String,
      default: ()=> require('../assets/left_back.svg')
    },
    svgIconRight: {
      type: String,
      default: ()=> ''
    },
    titleText: {
      type: String,
      default: ()=> ''
    },
  },
  data() {
    return {
      // left_back: require('../assets/left_back.svg')
    }
  },
  methods: {
    clickLeft() {
      // debugger
      if(this.svgIcon.includes('close')) {  //  如果为关闭的图标就调用父组件方法
        this.$emit('changeClose')
      } else {
        this.$router.back();
      }

    },
    clickRight() {
      this.$emit('clickRight')
    }
  }
}
</script>

<style lang='less' scoped>
  .nav-bar {
    box-sizing: border-box;
    padding: 0.75rem 0rem;
    ::v-deep .van-nav-bar__title {
      font-size: 1.375rem;
      font-weight: 500;
    }
  }
</style>